import React, {Component} from "react";
import {PropTypes} from "prop-types";
import OraModal from "../../core/components/ui/Modal";
import {OraButton} from "../../../components/OraCommonComponents";

class UnsupportedMsisdsModal extends Component {

    constructor(props){
        super(props);
        this.state ={
            ready: false,
            id: "UnsupportedMsisdsModalId"
        }
    }

    componentDidMount() {
        if (this.props.incorrectMsisdns && this.props.incorrectMsisdns.length > 0) {
            OPL.UI.on('module.started',(data) => {
                if(data.element.id === "react-modal-" + this.state.id + "-trigger"){
                    this.setState({ready: true})
                }
            });
        }
    }

    closeModal() {
        this.setState({ready: false})
    }

    render() {
            return ( !!this.props.incorrectMsisdns && this.props.incorrectMsisdns.length > 0 &&
                <OraModal open={this.state.ready} id={this.state.id} showClose={true} size="narrow">
                    <div className="l-row u-letter-spacing-xs">
                        <div className="l-col l-col-12">
                            <h3>
                                {this.props.descriptions.assignmentModalWarningTitle}
                            </h3>
                            <p>{this.props.descriptions.assignmentModalWarningMessage}</p>
                            <SimpleList props={this.props} list={this.props.incorrectMsisdns} />
                            <div className="l-col--opposite">
                                <OraButton onClick={() => this.closeModal()}>{this.props.descriptions.assignmentModalButton}</OraButton>
                            </div>
                        </div>
                    </div>
                </OraModal>
            )
    }
}

const SimpleList = ({ list, props }) => (
    <ul>
        {list.map(item => (
            <div className="u-padding-left-l u-padding-top-l">
                <div className="opl-msg opl-msg--error opl-msg--context" id={item.msisdn}>
                  <p>{item.msisdn}</p>
                </div>
                <b>{item.errorMessage}</b>
            </div>
        ))}
    </ul>
);

UnsupportedMsisdsModal.propTypes = {
    incorrectMsisdn: PropTypes.arrayOf(PropTypes.string)
};

export default UnsupportedMsisdsModal;